import classes from './cards.module.css'

const Card = ({ id, product, brand, price }) => {
    return (<div className={classes.card}>
        <img src='#' width={170} height={142} />
        <p>{product && product}</p>
        <p>{ brand && brand}</p>
        <div className={classes.cardBtn}>
            <p>{id && id }</p>
            <b>{price && price }</b>
        </div>
    </div> );
}
 
export default Card;