import { createSlice } from "@reduxjs/toolkit";
import { fetchProducts } from "./Thunk";

const initialState = {
    decorations: [],
    currentDecor: [],
    brands: [],
    text: "",
    status: null,
    error: null
}

export const decorationsSlice = createSlice({
    name: 'decorations',
    initialState,
    reducers: {
        setDecorations: (state, action) => {
            state.decorations = action.payload
            state.brands = action.payload.filter(item=>item?.brand)
        }, setCurrentDecor: (state, action) => {
            state.currentDecor = action.payload
        },
        setText: (state, action) => {
            state.text = action.payload
        },
    },

    // extraReducers: builder => {
    //     builder
    //         .addCase(fetchProducts.pending,(state)=>state.status = "loading")
    //         .addCase(fetchProducts.fulfilled, (state, action) => {
    //             state.status = 'resolved'
    //             state.decorations = action.payload;
    //             state.error = null;
    //     })
    //         .addCase(fetchProducts.rejected, (state, action) => {
    //             state.status = 'rejected'
    //             // state.error = action.payload;
    //         }); 
    // },
})

export const { setDecorations,setCurrentDecor, setText } = decorationsSlice.actions

export default decorationsSlice.reducer


