import classNames from 'classnames';
import classes from './nav.module.css'
import { Link, NavLink } from 'react-router-dom';
import { useState } from 'react';
import Range from '../Header/Range';
import { useSelector } from 'react-redux';
import RangePrice from '../../Pagers/RangePrice';

const MENU = ['Home', 'Brands', 'Price']

const Navigation = () => {

   
    const decorations = useSelector(state => state.decorationsReducer.decorations)

    const [activeIndex, setActiveIndex] = useState(0)
    
    return (<nav className={classes.navigate}>
        {
            MENU.map((item,i )=> (
                <Link key={item + i}
                    to={`/${item.toLowerCase()}`}
                    onClick={()=>setActiveIndex(i)}
                    className={classNames(classes.menu, {
                         [classes.active] : activeIndex === i
                     })}
                >{item}</Link>
            ))
        }
    
    </nav>  );
}
 
export default Navigation;




