import { useDispatch, useSelector } from "react-redux";
import Card from "./Card";
import classes from './cards.module.css'
import { useEffect,  } from "react";
import { URL_1, URL_2, password } from "../../helpers/constants";
import { setDecorations } from "../../Redux/decorationsSlice";
import Pagination from "../Pagination/Pagination";
import Skeleton from "./Skeleton";

const Cards = () => {

    const dispatch = useDispatch();
    const {currentDecor} = useSelector(state => state.decorationsReducer)
    const {decorations} = useSelector(state => state.decorationsReducer)
    const {text} = useSelector(state => state.decorationsReducer)

	useEffect(() => {
		fetch(URL_2, {
			method: 'post',
			headers: {
				"content-type": "application/json",
				"X-Auth": password
			},
			body: JSON.stringify({
				"action": "get_ids",
				"params": {}
			})
		})
			.then(res => res.json()).then(data => {
				const unicData = []
				const ids = new Set()
                for (const id of data.result) {
					if (!ids.has(id)) {
						ids.add(id)
						unicData.push(id)
					}
				}
				fetch(URL_2, {
					method: "post",
					headers: {
						"content-type": "application/json",
						"X-Auth": password
					},
					body: JSON.stringify({
						"action": "get_items",
						"params": {"ids":unicData}
					})
				})
                    .then(res => {
                        if (!res.ok) {
                            throw new Error(res.status);
                        }
                      return  res.json()
                    })
					.then(data => {
						dispatch(setDecorations(data.result))
						
					})
					.catch(err=>console.error('Error occurred:', err))
		})
	}, [])

    return (<div className={classes.cards}>
        {
          !decorations.length ? [...new Array(8)].map((item,i)=><Skeleton key={i}/>): 
				currentDecor
					.filter(item=>item.product.toLowerCase().includes(text.toLowerCase()))
					.map((decor, i) => {
                return (
                 
                <Card key={i} {...decor} />
            )
            })
        }	
    {decorations.length>1 && <Pagination itemsPerPage={50} items = {decorations} />}

    </div> );
}
 
export default Cards;