import classes from './header.module.css'
import { useState } from 'react';
import { useSelector } from 'react-redux';
import ReactSlider from 'react-slider';




const Range = () => {
    // const decorations = useSelector(state => state.decorationsReducer.decorations)
    const decorations = [13,11,15,3456,242,123,456]
    const sortPrice = decorations.sort((a,b)=>a.price>b.price)
    const [value,setValue] = useState([])
    let MIN = sortPrice[0];
    let MAX = sortPrice[sortPrice.length-1];
    
    console.log(MIN,MAX);
    return (<div className={classes.range}>
        
        <ReactSlider
            value={value}
            onChange={value=>setValue(value)}
    className={classes.rangeSlider}
    thumbClassName={classes.thumb}
    trackClassName={classes.track}
    defaultValue={[MIN, MAX]}
    // ariaLabel={['Lower thumb', 'Upper thumb']}
    // ariaValuetext={state => `Thumb value ${state.valueNow}`}
            renderThumb={(props, state) => {
                console.log(state);
                setValue(prev => {
                    prev = state
                    MIN = prev[0]
                    MAX = prev[1]
                });
        return <div {...props}>{state.valueNow }</div>
    }}
    pearling
    minDistance={10}
/>

    </div> );
}
 
export default Range;