import React from "react"
import ContentLoader from "react-content-loader"

const Skeleton = () => (
  <ContentLoader 
    speed={2}
    width={280}
    height={350}
    viewBox="0 0 300 350"
    backgroundColor="#f3f3f3"
    foregroundColor="#ecebeb"
  >
    <rect x="2" y="0" rx="24" ry="24" width="210" height="266" /> 
    <rect x="6" y="273" rx="0" ry="0" width="200" height="22" /> 
    <rect x="6" y="306" rx="5" ry="5" width="200" height="22" />
  </ContentLoader>
)

export default Skeleton