import { useState } from "react";
import { useSelector } from "react-redux";
import Card from "../Components/Cards/Card";
import classes from '../Components/Cards/cards.module.css'
import Pagination from "../Components/Pagination/Pagination";
import Skeleton from "../Components/Cards/Skeleton";
// import { setDecorations } from "../Redux/decorationsSlice";

const RangePrice = () => {
    
     const { decorations } = useSelector(state => state.decorationsReducer)
    let { currentDecor } = useSelector(state => state.decorationsReducer)
     currentDecor = currentDecor.slice()
    const [inc, setInc] = useState(true)
    
    return (<div className={classes.cards}>
        {<button className={classes.btnInc} onClick={() => setInc(prev => !prev)}>{inc ? "По убыванию" : "По возрастанию "}</button>} 
        {
            currentDecor.length > 1 ? (currentDecor.sort((a, b) => {
                if (inc) {
                    if (a.price < b.price) return -1;
                    if (a.price > b.price) return 1;    
                    return 0;
                } else {
                    if (a.price > b.price) return -1;
                    if (a.price < b.price) return 1;    
                    return 0;
                }
            }).map((item, i) => <Card key={i} {...item} />)) :
                [...Array(8)].map((item,i)=><Skeleton key={i}/>)
        }
    {currentDecor.length>1 && <Pagination itemsPerPage={50} items = {decorations} />}
        
    </div> );
}
 
export default RangePrice;