import React, { useEffect, useState } from 'react';
import ReactPaginate from 'react-paginate';
import classes from './pagination.module.css'
import { useDispatch } from 'react-redux';
import {  setCurrentDecor } from '../../Redux/decorationsSlice';

const Pagination = ({ itemsPerPage, items}) => {

  const dispatch = useDispatch()
  
  const [pageCount, setPageCount] = useState(0);
  const [itemOffset, setItemOffset] = useState(0);

  useEffect(() => {
    console.log('a,b');
    const endOffset = itemOffset + itemsPerPage;

    dispatch(setCurrentDecor(items?.slice(itemOffset, endOffset)));
    setPageCount(Math.ceil(items?.length / itemsPerPage));

  }, [itemOffset, itemsPerPage, items]);

  const handlePageClick = (event) => {
  const newOffset = (event.selected * itemsPerPage) % items.length;
  
    setItemOffset(newOffset);
    
  };

  return (
    <>
      <ReactPaginate
        className = {classes.pagination}
        breakLabel="..."
        breakClassName = {classes.breakLeb}
        nextLabel=">"
        onPageChange={handlePageClick}
        pageRangeDisplayed={3}
        pageCount={pageCount}
        previousLabel="<"
        pageClassName = {classes.li}
        pageLinkClassName = {classes.a} 
        activeLinkClassName = {classes.active}
        previousLinkClassName = {classes.a}
        nextLinkClassName = {classes.a}     
        renderOnZeroPageCount={null}
      />
    </>
  );
}

export default Pagination;
  