import { useSelector } from "react-redux";
import classes from '../Components/Cards/cards.module.css'
import Card from "../Components/Cards/Card";
import Skeleton from "../Components/Cards/Skeleton";
import Pagination from "../Components/Pagination/Pagination";

const Brands = () => {
    
    const { brands } = useSelector(state => state.decorationsReducer)
    const { currentDecor } = useSelector(state => state.decorationsReducer)
    return (<div className={classes.cards}>
        {
            currentDecor.length ? currentDecor.map((item, i) => <Card key={i} {...item} />) :
                [...Array(8)].map((item,i)=><Skeleton key={i}/>)
        }
    {currentDecor.length>1 && <Pagination itemsPerPage={50} items = {brands} />}
        
    </div> );
}
 
export default Brands;